html {
  font-family: 'Quicksand', 'Helvetica';
  background-image: radial-gradient(#bc90bc, #704370);
}
html,
body,
#app {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.btn,
button {
  display: inline-block;
  border-radius: .25em;
  padding: .25em 1em;
  font-size: .75em;
}
.big {
  font-size: 150px;
}
.settings {
  font-size: 50px;
  padding: 1em;
}
.settings button {
  font-weight: normal;
  display: inline-block;
  padding: 0;
  width: 75px;
  height: 75px;
  border-radius: 100px;
}
.settings p {
  display: inline-block;
}
.settings input {
  margin: 0;
  border: none;
  display: inline-block;
  line-height: inherit;
  background-color: transparent;
  color: #fff;
}
.settings input:focus {
  border: none;
  outline: none;
}
.settings input[type=number]::-webkit-outer-spin-button,
.settings input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.settings .sign {
  font-weight: bold;
  color: #70a970;
}
.score {
  font-size: 5em;
  padding: .25em;
  color: #f1e8e9;
  top: .5em;
  right: 1em;
}
.calc input {
  margin: 0;
  border: none;
  display: inline-block;
  line-height: inherit;
  background-color: transparent;
  color: #fff;
}
.calc input:focus {
  border: none;
  outline: none;
}
.calc input[type=number]::-webkit-outer-spin-button,
.calc input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.calc .sign {
  font-weight: bold;
  color: #70a970;
}
.smiley {
  display: inline-block;
  padding: 0;
  width: 200px;
  height: 200px;
  border-radius: 100px;
}
.messenger {
  z-index: 10;
  width: 100%;
  padding: 50px;
}
.messenger .messenger-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  margin-bottom: 5px;
  -webkit-box-shadow: 2.5px 2.5px 2.5px 0px rgba(45, 42, 38, 0.4);
          box-shadow: 2.5px 2.5px 2.5px 0px rgba(45, 42, 38, 0.4);
}
.messenger .messenger-content .icon {
  padding-right: 10px;
}
.messenger .messenger-content.danger {
  background-color: #df7666;
  color: #642015 !important;
}
.messenger .messenger-content.danger .icon i:before {
  content: "\F00D";
}
.messenger .messenger-content.success {
  background-color: #66df76;
  color: #156420 !important;
}
.messenger .messenger-content.success .icon i:before {
  content: "\F00C";
}
.messenger .messenger-content.warning {
  background-color: #cfdf66;
  color: #596415 !important;
}
.messenger .messenger-content.warning .icon i:before {
  content: "\F071";
}
.messenger .messenger-content.info {
  background-color: #66cfdf;
  color: #155964 !important;
}
.messenger .messenger-content.info .icon i:before {
  content: "\F129";
}
.wait {
  z-index: 11;
  position: fixed;
  top: 0;
  left: 0;
  right: auto;
  bottom: auto;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.75);
  color: #d96c47;
}
.modal {
  z-index: 11;
  position: fixed;
  top: 0;
  left: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  bottom: auto;
  right: auto;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 800px;
  max-width: 80%;
}
.modal .modal-content {
  width: 100%;
  height: 500px;
  overflow-y: auto;
}
.modal button {
  position: absolute;
  top: 0;
  left: auto;
  right: 0;
  bottom: auto;
  font-size: 2em;
  width: 40px;
  height: 40px;
  background-color: transparent;
}
.modal button:hover {
  background-color: #f6c400;
}
.modal button i {
  position: absolute;
  top: 50%;
  left: 50%;
  bottom: auto;
  right: auto;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  padding: 0;
}
.modal.splash .modal-content {
  background-color: #ffd42a;
}
.modal.splash img {
  height: 75%;
  position: absolute;
  top: 50%;
  left: 50%;
  bottom: auto;
  right: auto;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.modal.splash p {
  position: absolute;
  top: auto;
  left: auto;
  right: 0;
  bottom: 0;
  padding: 10px;
}
